<template>
  <div class="card-user">
    <span class="card-user_name">
      {{user.name}}
    </span>
    <span class="card-user_score">
      {{user.scores[datetime]}}
      <i class="material-icons">&#xE885;</i>
    </span>
  </div>
</template>

<script>
export default {
  name: 'score-user',
  date () {
    return {
      user: {}
    }
  },
  props: {
    user: Object,
    datetime: String,
    score: Number
  },
  methods: {
    getUser () {
      const users = this.$store.getters.getUsers
      const user = users[this.userId]
      if (user) {
        this.user = user
        return user
      }
      this.fetchUser()
    },
    fetchUser () {
      this.$store.dispatch('getUser', this.userId).then(() => {
        this.getUser()
      })
    }
  },
  mounted () {
    this.getUser()
  }
}
</script>

<style>

</style>



